import { isEnhancedTextBlock } from "@rp/react/rpjson/types"
import { EnhancedDocumentTypes, EnhancedElementBlock } from "@rp/react/rpjson/types"
import { createEvent } from "./create-event"
import { RpJsonEvent } from "./create-react-elements.types"

export const parseCoordinates = (
  onHover: (original: React.MouseEvent, event: RpJsonEvent) => void = () => {},
  event: React.MouseEvent,
  block: EnhancedElementBlock,
) => {
  const coordinates = isEnhancedTextBlock(block) ? block.normalized_coordinates : []
  return onHover(event, createEvent({ coordinates }))
}

export const getTitle = (tree: Array<string>, { addTitle = false } = {}) => {
  if (!addTitle) return ""
  if (!tree) return ""
  if (!tree.length) return EnhancedDocumentTypes.PARAGRAPH
  return tree[tree.length - 1]
}
